// src/component/analytics/ProfileMatch.jsx
"use client";

import dynamic from "next/dynamic";
import Highcharts from "highcharts";
import React, { useEffect, useState } from "react";
import { API_URL } from "../api/apiURL";

const HighchartsReact = dynamic(() => import("highcharts-react-official"), { ssr: false });

// helper to darken/lighten colors
function shadeColor(color, percent) {
  let R = parseInt(color.substring(1, 3), 16);
  let G = parseInt(color.substring(3, 5), 16);
  let B = parseInt(color.substring(5, 7), 16);

  R = parseInt((R * (100 + percent)) / 100);
  G = parseInt((G * (100 + percent)) / 100);
  B = parseInt((B * (100 + percent)) / 100);

  R = R < 255 ? R : 255;
  G = G < 255 ? G : 255;
  B = B < 255 ? B : 255;

  return (
    "#" +
    (R.toString(16).length === 1 ? "0" + R.toString(16) : R.toString(16)) +
    (G.toString(16).length === 1 ? "0" + G.toString(16) : G.toString(16)) +
    (B.toString(16).length === 1 ? "0" + B.toString(16) : B.toString(16))
  );
}

export default function ProfileMatch() {
  const months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];

  const [pieData, setPieData] = useState([]);
  const [totalUsers, setTotalUsers] = useState(new Array(12).fill(0));
  const [matches, setMatches] = useState(new Array(12).fill(0));

  // Load Profile Overview
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(API_URL + "/admin/overview");
        const api = await res.json();
        if (!api.success) return;

        const order = ["Completed", "Low", "Incomplete", "Moderate"];
        const colorMap = {
          Completed: "#0c7c1b",
          Low: "#f6931d",
          Incomplete: "#e21d1d",
          Moderate: "#f6c927",
        };

        setPieData(
          order.map((label) => {
            const found = api.data.find((x) => x.name === label);
            return { name: label, y: found ? found.value : 0, color: colorMap[label] };
          })
        );
      } catch (err) {
        console.error("Profile overview fetch error:", err);
      }
    })();
  }, []);

  // Load matches per month
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(API_URL + "/admin/matches-per-month");
        const api = await res.json();
        if (!api.success) return;

        setTotalUsers(months.map((m) => api.data.find((x) => x.month === m)?.totalUsers || 0));
        setMatches(months.map((m) => api.data.find((x) => x.month === m)?.matches || 0));
      } catch (err) {
        console.error("matches-per-month fetch error:", err);
      }
    })();
  }, []);

  // PIE OPTIONS (3D simulation)
  const pieOptions = {
    chart: { type: "pie", backgroundColor: "transparent", height: 340 },
    title: { text: "" },
    credits: { enabled: false },
    legend: { enabled: false },
    tooltip: {
      useHTML: true,
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
      formatter() {
        const total = this.series.chart.series[1].data.reduce((s, p) => s + (p.y || 0), 0) || 1;
        const pct = ((this.point.y / total) * 100).toFixed(1);
        return `<div style="font-weight:700;color:#222">${this.point.name}</div>
                <div style="margin-top:4px">${this.point.y} Users &nbsp; (<span style="font-weight:700">${pct}%</span>)</div>`;
      },
    },
    plotOptions: {
      pie: {
        cursor: "pointer",
        dataLabels: { enabled: false },
        startAngle: -50,
        endAngle: 310,
        center: ["35%", "50%"],
        size: 260,
      },
    },

    // double pie for 3D look
    series: [
      // bottom shadow donut
      {
        type: "pie",
        innerSize: "62%",
        data: pieData.map((d) => ({
          name: d.name,
          y: d.y,
          color: shadeColor(d.color, -20),
          enableMouseTracking: false,
          borderColor: "transparent",
        })),
        dataLabels: { enabled: false },
        zIndex: 0,
        states: { hover: { enabled: false } },
      },

      // top slices
      {
        type: "pie",
        innerSize: "52%",
        data: pieData.map((d) => ({
          name: d.name,
          y: d.y,
          slicedOffset: 12,
          borderColor: "#fff",
          borderWidth: 6,
          color: {
            linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
            stops: [
              [0, shadeColor(d.color, 10)],
              [0.5, d.color],
              [1, shadeColor(d.color, -8)],
            ],
          },
        })),
        dataLabels: { enabled: false },
        zIndex: 2,
      },
    ],
  };

  // MATCH OPTIONS
  const matchOptions = {
    chart: { backgroundColor: "transparent", height: 420, spacing: [10, 25, 10, 25] },
    title: { text: "" },
    credits: { enabled: false },
    legend: { enabled: false },

    xAxis: {
      categories: months,
      labels: { style: { fontSize: "14px", fontWeight: 700, color: "#444" } },
      lineColor: "#ededed",
    },

    yAxis: {
      min: 0,
      max: 35,
      tickInterval: 5,
      gridLineColor: "#e6e6e6",
      labels: {
        formatter() { return this.value + " M"; },
        style: { fontWeight: 700, color: "#666" },
      },
      title: { text: "" },
    },

    tooltip: {
      shared: true,
      useHTML: true,
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
      formatter() {
        let html = `<div style="font-weight:700;margin-bottom:6px">${this.x}</div>`;
        this.points.forEach((pt) => {
          html += `<div style="margin:4px 0;display:flex;align-items:center">
                    <span style="display:inline-block;width:10px;height:10px;background:${pt.color};border-radius:2px;margin-right:8px"></span>
                    <strong>${pt.series.name}:</strong>&nbsp; ${pt.y}
                   </div>`;
        });
        return html;
      },
    },

    plotOptions: {
      column: {
        borderRadius: 20,
        pointPadding: 0.15,
        borderWidth: 0,
        pointWidth: 28,
        shadow: {
          color: "rgba(0,0,0,0.18)",
          offsetX: 0,
          offsetY: 6,
          width: 16,
        },
      },
    },

    series: [
      {
        type: "column",
        name: "Total no. of Users",
        color: {
          linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
          stops: [
            [0, "#f7d34a"],
            [0.5, "#f3c64a"],
            [1, "#d7a304"],
          ],
        },
        data: totalUsers,
      },
      {
        type: "line",
        name: "No. Of Matches",
        color: "#e52b2b",
        lineWidth: 4,
        marker: {
          enabled: true,
          radius: 7,
          fillColor: "#fff",
          lineColor: "#e52b2b",
          lineWidth: 2,
        },
        data: matches,
      },
    ],
  };

  const legendBoxStyle = {
    width: 16,
    height: 16,
    borderRadius: 4,
    background: "#fff",
    boxSizing: "border-box",
  };

  return (
    <div className="w-full p-6">
      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6 items-start">

        {/* LEFT PIE CHART */}
        <div className="bg-white rounded-2xl shadow-xl p-6 relative" style={{ border: "2px solid rgba(0,0,0,0.08)", minHeight: 360 }}>
          <h2 style={{ fontSize: 26, fontWeight: 700, color: "#222" }}>Profile Overview</h2>

          {/* vertical legend */}
          <div style={{ position: "absolute", right: 28, top: 22, display: "flex", flexDirection: "column", gap: 14 }}>
            {[
              ["Completed", "#0c7c1b"],
              ["Low", "#f6931d"],
              ["Incomplete", "#e21d1d"],
              ["Moderate", "#f6c927"],
            ].map((item) => (
              <div key={item[0]} style={{ display: "flex", alignItems: "center", gap: 10 }}>
                <div style={{ ...legendBoxStyle, border: `2.5px solid ${item[1]}` }} />
                <span style={{ fontWeight: 700 }}>{item[0]}</span>
              </div>
            ))}
          </div>

          <div style={{ marginTop: 36 }}>
            <HighchartsReact highcharts={Highcharts} options={pieOptions} />
          </div>
        </div>

        {/* RIGHT BAR/LINE CHART */}
        <div className="bg-white rounded-2xl shadow-xl p-6 relative" style={{ border: "2px solid rgba(0,0,0,0.08)", minHeight: 520 }}>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <h2 style={{ fontSize: 26, fontWeight: 700, color: "#222" }}>Matches Per Month</h2>

            {/* horizontal legend */}
            <div style={{ display: "flex", gap: 40, fontWeight: 700, color: "#333" }}>
              <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
                <div style={{ ...legendBoxStyle, border: "2.5px solid #d7a304" }} />
                Total no. of Users
              </div>
              <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
                <div style={{ ...legendBoxStyle, border: "2.5px solid #e52b2b" }} />
                No. Of Matches
              </div>
            </div>
          </div>

          <div style={{ marginTop: 28 }}>
            <HighchartsReact highcharts={Highcharts} options={matchOptions} />
          </div>
        </div>

      </div>
    </div>
  );
}
