// src/component/analytics/SearchAndTrend.jsx
"use client";

import dynamic from "next/dynamic";
import Highcharts from "highcharts";
import React, { useEffect, useState } from "react";
import { API_URL } from "../api/apiURL";

const HighchartsReact = dynamic(() => import("highcharts-react-official"), { ssr: false });

// Developer note: uploaded file path (toolchain will transform to URL if required)
const REF_IMAGE = "/mnt/data/Screenshot 2025-11-25 161745.png";

// helper to darken/lighten colors
function shadeColor(color, percent) {
  let R = parseInt(color.substring(1, 3), 16);
  let G = parseInt(color.substring(3, 5), 16);
  let B = parseInt(color.substring(5, 7), 16);

  R = parseInt((R * (100 + percent)) / 100);
  G = parseInt((G * (100 + percent)) / 100);
  B = parseInt((B * (100 + percent)) / 100);

  R = R < 255 ? R : 255;
  G = G < 255 ? G : 255;
  B = B < 255 ? B : 255;

  const toHex = (v) => (v.toString(16).length === 1 ? "0" + v.toString(16) : v.toString(16));
  return `#${toHex(R)}${toHex(G)}${toHex(B)}`;
}

// format large numbers into K (with two decimals) like "919.98 K"
function fmtK(n) {
  if (n === null || n === undefined) return "-";
  if (Math.abs(n) >= 1000) return (n / 1000).toFixed(2) + " K";
  return n.toString();
}

export default function SearchAndTrend() {
  // left chart items (order from screenshot top->bottom)
  const leftItems = [
    { key: "userSearched", label: "User Searched" },
    { key: "profileViewed", label: "Profile Viewed" },
    { key: "expressedInterest", label: "Expressed Interest" },
    { key: "matchedSuccessfully", label: "Matched Successfully" },
  ];

  const week = ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"];

  // state
  const [leftValues, setLeftValues] = useState({
    userSearched: 919980,
    profileViewed: 876700,
    expressedInterest: 597120,
    matchedSuccessfully: 299220,
  });
  const [newRegistered, setNewRegistered] = useState([25, 22, 16, 18, 21, 27, 29]); // default per-screenshot style
  const [returningUsers, setReturningUsers] = useState([10, 15, 13, 12, 14, 20, 24]);
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState(null);

  // fetch data from APIs
  useEffect(() => {
    let mounted = true;
    setLoading(true);
    setErr(null);

    async function fetchAll() {
      try {
        // Fetch left (search-to-match)
        const leftRes = await fetch(API_URL + "/admin/search-to-match");
        const leftJson = await leftRes.json();

        // Fetch right (running user trend)
        const rightRes = await fetch(API_URL + "/admin/running-user-trend");
        const rightJson = await rightRes.json();

        if (!mounted) return;

        // left mapping - robust: accept either { success, data } or plain object
        if (leftJson && leftJson.success && leftJson.data) {
          const data = leftJson.data;
          setLeftValues((prev) => ({
            userSearched: data.userSearched ?? data.userSearchedCount ?? prev.userSearched,
            profileViewed: data.profileViewed ?? data.profileViewedCount ?? prev.profileViewed,
            expressedInterest: data.expressedInterest ?? data.expressedInterestCount ?? prev.expressedInterest,
            matchedSuccessfully: data.matchedSuccessfully ?? data.matchedSuccessfullyCount ?? prev.matchedSuccessfully,
          }));
        } else if (leftJson && typeof leftJson === "object" && ("userSearched" in leftJson || "profileViewed" in leftJson)) {
          setLeftValues({
            userSearched: leftJson.userSearched ?? leftValues.userSearched,
            profileViewed: leftJson.profileViewed ?? leftValues.profileViewed,
            expressedInterest: leftJson.expressedInterest ?? leftValues.expressedInterest,
            matchedSuccessfully: leftJson.matchedSuccessfully ?? leftValues.matchedSuccessfully,
          });
        }

        // right mapping - expecting array of objects with day and values
        if (rightJson && rightJson.success && Array.isArray(rightJson.data)) {
          const arr = rightJson.data;
          // try to map using week codes or day names
          const newReg = [];
          const retUsers = [];
          for (const d of week) {
            // find item where day matches (allow full name or 2-letter or 3-letter)
            const found = arr.find(
              (x) =>
                x.day === d ||
                x.day === d.toLowerCase() ||
                (typeof x.day === "string" && x.day.slice(0, 2).toLowerCase() === d.slice(0, 2).toLowerCase()) ||
                (x.dayName && x.dayName.slice(0, 2).toLowerCase() === d.slice(0, 2).toLowerCase())
            );
            newReg.push(found ? (found.newRegistered ?? found.new ?? 0) : 0);
            retUsers.push(found ? (found.returningUsers ?? found.returning ?? 0) : 0);
          }
          setNewRegistered(newReg);
          setReturningUsers(retUsers);
        } else if (rightJson && Array.isArray(rightJson)) {
          // in case direct array returned
          const arr = rightJson;
          const newReg = [];
          const retUsers = [];
          for (const d of week) {
            const found = arr.find((x) => x.day === d || x.day.slice(0, 2) === d);
            newReg.push(found ? (found.newRegistered ?? found.new ?? 0) : 0);
            retUsers.push(found ? (found.returningUsers ?? found.returning ?? 0) : 0);
          }
          setNewRegistered(newReg);
          setReturningUsers(retUsers);
        }

        setLoading(false);
      } catch (e) {
        console.error("fetch error", e);
        setErr(e.message || "Fetch error");
        setLoading(false);
      }
    }

    fetchAll();

    return () => {
      mounted = false;
    };
  }, []);

  // --- LEFT: Horizontal bars options (Highcharts) ---
  // We'll create a "bar" chart with categories placed on yAxis (reversed for top-first order)
  const barColors = {
    main: "#3046ff", // strong blue
    glossyTop: "#5b6bff",
  };

  const leftCategories = leftItems.map((it) => it.label);
  const leftData = leftItems.map((it) => leftValues[it.key] ?? 0);

  const barOptions = {
    chart: {
      type: "bar",
      backgroundColor: "transparent",
      height: 260,
    },
    title: { text: "" },
    credits: { enabled: false },
    legend: { enabled: false },
    xAxis: {
      categories: leftCategories,
      reversed: true,
      labels: {
        align: "left",
        style: { fontSize: "15px", fontWeight: 700, color: "#333" },
      },
      lineWidth: 0,
      tickLength: 0,
    },
    yAxis: {
      min: 0,
      visible: false,
    },
    plotOptions: {
      series: {
        grouping: false,
        borderRadius: 30,
        pointPadding: 0.25,
      },
      bar: {
        borderRadius: 30,
        pointWidth: 22,
        dataLabels: {
          enabled: false,
        },
      },
    },
    tooltip: {
      enabled: false,
    },
    series: [
      {
        // invisible base filler so bars start from left inside the container nicely
        name: "base",
        data: leftData.map((v) => 0),
        showInLegend: false,
        color: "transparent",
      },
      {
        name: "Count",
        data: leftData,
        color: {
          linearGradient: { x1: 0, y1: 0, x2: 1, y2: 0 },
          stops: [
            [0, shadeColor(barColors.main, 18)], // slightly lighter left
            [0.5, barColors.main],
            [1, shadeColor(barColors.main, -12)], // darker right
          ],
        },
        showInLegend: false,
        dataLabels: {
          enabled: false,
        },
      },
    ],
  };

  // We'll overlay right-side labels manually using a small helper below

  // --- RIGHT: Running User Trend options ---
  const trendOptions = {
    chart: { backgroundColor: "transparent", height: 320 },
    title: { text: "" },
    credits: { enabled: false },
    legend: { enabled: false },
    xAxis: {
      categories: week,
      labels: { style: { fontSize: "14px", fontWeight: 700, color: "#444" } },
      lineColor: "#eee",
      tickLength: 0,
    },
    yAxis: {
      min: 0,
      max: 30, // adjust based on API data if desired
      tickInterval: 5,
      gridLineColor: "#e6e6e6",
      labels: { style: { fontWeight: 700, color: "#666" }, formatter() { return this.value + " M"; } },
      title: { text: "" },
    },
    tooltip: {
      shared: true,
      useHTML: true,
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
      formatter() {
        let html = `<div style="font-weight:700;margin-bottom:6px">${this.x}</div>`;
        (this.points || []).forEach((pt) => {
          html += `<div style="margin:4px 0;display:flex;align-items:center">
                    <span style="display:inline-block;width:10px;height:10px;background:${pt.color};border-radius:50%;margin-right:8px"></span>
                    <strong style="font-weight:700">${pt.series.name}:</strong>&nbsp; ${pt.y}
                   </div>`;
        });
        return html;
      },
    },
    plotOptions: {
      series: { states: { hover: { enabled: true } } },
    },
    series: [
      {
        type: "line",
        name: "New Registered Users",
        data: newRegistered,
        color: "#f7d34a",
        lineWidth: 3.5,
        marker: { enabled: true, radius: 6, fillColor: "#ffffff", lineColor: "#f7d34a", lineWidth: 2 },
      },
      {
        type: "line",
        name: "Returning Users",
        data: returningUsers,
        color: "#1fa65a",
        lineWidth: 3.5,
        marker: { enabled: true, radius: 6, fillColor: "#ffffff", lineColor: "#1fa65a", lineWidth: 2 },
      },
    ],
  };

  // styling for the small square legend icons
  const legendBoxStyle = {
    width: 18,
    height: 18,
    borderRadius: 4,
    background: "#fff",
    boxSizing: "border-box",
  };

  return (
    <div className="w-full p-6">
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 items-start">
        {/* LEFT: Search To Match */}
        <div
          className="bg-white rounded-2xl shadow-xl p-6 relative"
          style={{ border: "2px solid rgba(0,0,0,0.08)", minHeight: 260 }}
        >
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start" }}>
            <h3 style={{ fontSize: 22, fontWeight: 700, color: "#222" }}>Search To Match</h3>
            <div />
          </div>

          {/* chart area */}
          <div style={{ display: "flex", gap: 18, alignItems: "center", marginTop: 14 }}>
            <div style={{ flex: 1, minWidth: 0 }}>
              <HighchartsReact highcharts={Highcharts} options={barOptions} />
            </div>

            {/* right-side values (aligned with categories) */}
            <div style={{ width: 110, display: "flex", flexDirection: "column", justifyContent: "center", gap: 22 }}>
              {/* We'll display values in same order top->bottom */}
              {leftItems.map((it) => (
                <div key={it.key} style={{ textAlign: "right", fontWeight: 700, color: "#222", minWidth: 0 }}>
                  {fmtK(leftValues[it.key])}
                </div>
              ))}
            </div>
          </div>

          {/* subtle footnote / image reference (not visible by default) */}
          <img src={REF_IMAGE} alt="" style={{ display: "none" }} />
        </div>

        {/* RIGHT: Running User Trend */}
        <div
          className="bg-white rounded-2xl shadow-xl p-6 relative"
          style={{ border: "2px solid rgba(0,0,0,0.08)", minHeight: 320 }}
        >
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            <h3 style={{ fontSize: 22, fontWeight: 700, color: "#222" }}>Running User Trend</h3>

            {/* legend */}
            <div style={{ display: "flex", gap: 18, alignItems: "center", fontWeight: 700, color: "#333" }}>
              <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
                <div style={{ ...legendBoxStyle, border: "3px solid #f7d34a" }} />
                <span>New Registered Users</span>
              </div>
              <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
                <div style={{ ...legendBoxStyle, border: "3px solid #1fa65a" }} />
                <span>Returning Users</span>
              </div>
            </div>
          </div>

          <div style={{ marginTop: 16 }}>
            <HighchartsReact highcharts={Highcharts} options={trendOptions} />
          </div>
        </div>
      </div>

      {/* status / errors */}
      <div style={{ marginTop: 12 }}>
        {loading ? (
          <div style={{ color: "#666", fontWeight: 700 }}>Loading charts...</div>
        ) : err ? (
          <div style={{ color: "crimson", fontWeight: 700 }}>Error loading data: {err}</div>
        ) : null}
      </div>
    </div>
  );
}
